import remToPx from "./remToPx";

const parsePx = (value) => {
	if (typeof value === "number") return value;
	if (typeof value === "string") {
		if (value.indexOf("rem") > 0) {
			const rem = parseFloat(value);
			if (!isNaN(rem)) {
				return remToPx(rem);
			}
		}
		const px = parseFloat(value);
		if (!isNaN(px)) {
			return px;
		}
	}
	return undefined;
}

export default parsePx;
