import $ from "jquery";
import videojs from "video.js";
import { createPopper } from '@popperjs/core';
import { createPlacementHandler, sameWidth } from "./utils/popperModifiers";
import copyToClipboard from "./utils/copyToClipboard";
import ScrollBooster from 'scrollbooster';
import isTouchEnabled from "./utils/isTouchEnabled";
import parsePx from "./utils/parsePx";

// new way
import addDragToScrollLogic from "./components/dragToScroll";
import addMediaViewerLogic from "./components/mediaViewer.ts";

import cssVariables from "../css/variables.module.scss";
import { throttle } from "throttle-debounce";

const ANIMATION_SLOW_MS = parseFloat(cssVariables["animation-slow"]) * 1000;
const ANIMATION_NORMAL_MS = parseFloat(cssVariables["animation-normal"]) * 1000;
const POPPER_VIEWPORT_PADDING = parsePx(cssVariables["popper-viewport-padding"]);

$(document).ready(function () {

	// popper

	$('.popper').each((i, popperMenu) => {
		const onHover = $(popperMenu).data('popper-on-hover') !== undefined;
		const anchorElement = $($(popperMenu).data('popper-anchor-element'));
		const closeElements = $(popperMenu).find('[data-popper-close-element]')

		let overlay;
		let popper;
		let closingTimeout;

		const openPopper = (currentAnchorElement, _options) => {
			const options = {
				disableOverlay: false,
				..._options,
			}
			clearTimeout(closingTimeout);
			if (!popper) popper = createPopper(currentAnchorElement, popperMenu, {
				strategy: "fixed",
				modifiers: [{

					name: 'preventOverflow',
					options: {
						altAxis: true,
						padding: POPPER_VIEWPORT_PADDING
					},
				}]
			});
			if (!options.disableOverlay) {
				overlay = $("<div>")
					.addClass("popper-overlay")
					.on("click", closePopper)
					.prependTo("body");
			}
			$(popperMenu).addClass("open");
		}
		const closePopper = () => {
			$(overlay).remove();
			overlay = undefined;
			$(popperMenu).removeClass("open");
			clearTimeout(closingTimeout);
			closingTimeout = setTimeout(() => {
				popper.destroy();
				popper = undefined;
			}, ANIMATION_SLOW_MS)
		}

		if (onHover) {
			$(anchorElement)
				.on("mouseenter", (e) => {
					const isOpen = $(popperMenu).hasClass("open");

					if (!isOpen) {
						openPopper(e.currentTarget, { disableOverlay: true });
					}
				})
				.on("mouseleave", () => {
					const isOpen = $(popperMenu).hasClass("open");

					if (isOpen) {
						closePopper();
					}
				})
		}
		else {
			$(closeElements)
				.on("click", () => {
					closePopper();
				});
			$(anchorElement)
				.on("click", (e) => {
					const isOpen = $(popperMenu).hasClass("open");
					if (!isOpen) {
						openPopper(e.currentTarget);
					}
					else {
						closePopper();
					}
				});
		}

	});

	// select

	$('.select').each((i, select) => {
		const input = $(select).find("input").get(0);

		// get trigger

		const trigger = $(select).find(".select__trigger").get(0);
		const triggerValue = $(trigger).find(".select__trigger-value").get(0);
		const menu = $(select).find(".select__menu").get(0);
		const menuItems = $(select).find(".select__menu-item");

		// helpers

		let overlay;

		const openMenu = () => {
			overlay = $("<div class='select__overlay'></div>")
				.click((e) => {
					$(e.currentTarget).remove();
					$(select).removeClass("open");
					$(menu).removeClass("open");
				})
				.prependTo("body");
			$(select).addClass("open");
			$(menu).addClass("open");
		};

		const closeMenu = () => {
			$(overlay).remove();
			$(select).removeClass("open");
			$(menu).removeClass("open");
		};

		const toggleMenu = () => {
			const isOpen = $(select).hasClass("open");

			if (!isOpen) {
				openMenu();
			}
			else {
				closeMenu();
			}
		};

		const updateSelected = () => {
			const value = $(input).val();
			const selected = $(menuItems).filter(`*[data-value="${value}"]`);
			$(selected).addClass("selected").siblings().removeClass("selected");
			$(triggerValue).html($(selected).html());
		}

		// prepare

		updateSelected();

		const popper = createPopper($(trigger).get(0), $(menu).get(0), {
			strategy: "fixed",
			modifiers: [
				sameWidth
			]
		});

		// add trigger events

		$(trigger).click(() => {
			// without second update it has wrong position
			popper.update().then(() => {
				popper.update().then(() => {
					toggleMenu();
				})
			});
		});

		$(menuItems).click((e) => {
			const value = $(e.currentTarget).attr("data-value");
			$(input).val(value);
			$(input).get(0).dispatchEvent(new Event('change'));
			closeMenu();
		})

		$(input).change(() => {
			updateSelected();
		})
	});

	// input

	$('.input').each((i, input) => {
		const realInput = $(input).find("input").get(0);

		$(input)
			.click(() => $(realInput).focus());

		$(realInput)
			.focus(() => $(input).addClass("focus"))
			.blur(() => $(input).removeClass("focus"));
	});

	$('.input-password').each((i, input) => {
		const realInput = $(input).find("input").get(0);
		const eyeBtn = $(input).find(".input-password__eye-btn").get(0);

		const turnEyeOn = () => {
			$(input).addClass("eye-on");
			$(input).removeClass("eye-off");
			$(realInput).attr("type", "text");
		}

		const turnEyeOff = () => {
			$(input).addClass("eye-off");
			$(input).removeClass("eye-on");
			$(realInput).attr("type", "password");
		}

		$(eyeBtn).click((e) => {
			e.stopPropagation();
			if ($(input).hasClass("eye-on")) {
				turnEyeOff();
			}
			else {
				turnEyeOn();
			}
		})
	})

	// modal

	const closeModal = (modal) => {
		$(modal).removeClass("open");
		$(document.documentElement).removeClass("modal-open");
	};

	const openModal = (modal) => {
		$(modal).addClass("open");
		$(document.documentElement).addClass("modal-open");
	};

	$('.modal').each((i, modal) => {
		$(modal).addClass("ready");
		$(modal).find(".modal__close-btn").click(() => closeModal(modal));
		$(modal).find(".modal__content").click((e) => {
			if (e.target === e.currentTarget) {
				closeModal(modal);
			}
		});
	});

	$('.modal-trigger').each((i, modalTrigger) => {
		const modal = $($(modalTrigger).attr("data-modal-trigger-target"));
		$(modalTrigger).click(() => openModal(modal));
	});

	// copy text

	$('.copy-text').each((i, copyText) => {
		const value = $(copyText).find(".copy-text__value").text().trim();
		const tooltip = $(copyText).find(".copy-text__success-tooltip").get(0);
		document.body.appendChild(tooltip);

		if (!tooltip) return;

		const placement = (tooltip.className.match(/left|right|top|bottom/g) || [])[0]

		const popper = createPopper(copyText, tooltip, {
			placement: placement,
			strategy: "absolute",
			modifiers: [
				{
					name: "flip",
					options: {
						fallbackPlacements: ['auto'],
					},
				},
				createPlacementHandler((placement, element) => {
					$(element).removeClass("top");
					$(element).removeClass("left");
					$(element).removeClass("right");
					$(element).removeClass("bottom");
					$(element).addClass(placement);
				}),
			],
		});

		let hidingTimeout;

		const showTooltip = () => {
			popper.update();
			clearTimeout(hidingTimeout);
			$(tooltip).addClass("show").removeClass("fade-out-slow");
		}

		const hideTooltip = (options = {}) => {
			clearTimeout(hidingTimeout);

			if (options.rightNow) {
				$(tooltip).removeClass("show").removeClass("fade-out-slow");
			}
			else {
				$(tooltip).addClass("fade-out-slow");

				hidingTimeout = setTimeout(() => {
					$(tooltip).removeClass("show").removeClass("fade-out-slow");
				}, ANIMATION_SLOW_MS);
			}
		}

		let autoHidingTimeout;

		$(copyText).click(() => {
			copyToClipboard(value).then(() => {
				showTooltip();

				clearTimeout(autoHidingTimeout);
				autoHidingTimeout = setTimeout(() => {
					hideTooltip();
				}, 2000);
			});
		})

		$(document).click((e) => {
			if ($(copyText).has(e.target).length === 0 && !$(e.target).hasClass("copy-text")) {
				hideTooltip({ rightNow: true });
			}
		});
	});

	// promo card

	$(".promo-card").each((i, promoCard) => {
		const more = $(promoCard).find(".promo-card__content-more").get(0);
		const expandBtn = $(promoCard).find(".promo-card__expand-btn").get(0);
		const collapseBtn = $(promoCard).find(".promo-card__collapse-btn").get(0);

		$(expandBtn).click(() => {
			$(more).addClass("expand");
			$(expandBtn).removeClass("active");
			$(collapseBtn).addClass("active");

			const animationTime = Math.sqrt(more.scrollHeight) / 20;

			$(more).css("height", `${more.scrollHeight}px`);
			$(more).css("transition", `height ${animationTime}s`);
		});

		$(collapseBtn).click(() => {
			$(more).removeClass("expand");
			$(collapseBtn).removeClass("active");
			$(expandBtn).addClass("active");

			const animationTime = Math.sqrt(more.scrollHeight) / 20;

			$(more).css("height", "0px");
			$(more).css("transition", `height ${animationTime}s`);
		});
	});

	// tabs (e.g. in materials)

	$(".tabs").each((i, tabs) => {
		const menu = tabs.querySelector(".tabs__menu")
		const menuItems = [...tabs.querySelectorAll(".tabs__menu-item")];
		const moreItems = tabs.querySelector(".tabs__menu-more-items");
		const moreItemsMenu = tabs.querySelector(".tabs__menu-more-items-menu");
		const moreItemsMenuItems = [...tabs.querySelectorAll(".menu__item")];
		const contentItems = [...tabs.querySelectorAll(".tabs__content-item")];

		const setActive = (index) => {
			menuItems.forEach(item => item.classList.remove("active"));
			moreItemsMenuItems.forEach(item => item.classList.remove("selected"));
			contentItems.forEach(item => item.classList.remove("active"));

			menuItems[index]?.classList.add("active");
			moreItemsMenuItems[index]?.classList.add("selected");
			contentItems[index]?.classList.add("active");

			if (!moreItemsMenuItems[index].classList.contains("hidden")) {
				moreItems.classList.add("active");
			}
			else {
				moreItems.classList.remove("active");
			}
		}

		const updateMenu = throttle(250, () => {
			let visibleWidth = moreItems.clientWidth;
			let lastVisible = -1;
			menuItems.forEach((menuItem, index) => {
				visibleWidth += menuItem.clientWidth;
				if (visibleWidth < menu.clientWidth) {
					lastVisible = index;
				}
			});

			// if all visible
			if (lastVisible === menuItems.length - 1) {
				moreItems.classList.add("hidden");
			}
			else {
				moreItems.classList.remove("hidden");
			}
			// hide overflowed
			menuItems.forEach((menuItem, index) => {
				if (index <= lastVisible) {
					menuItem.classList.remove("hidden");
				}
				else {
					menuItem.classList.add("hidden");
				}
			});
			moreItemsMenuItems.forEach((item, index) => {
				if (index > lastVisible) {
					item.classList.remove("hidden");
				}
				else {
					item.classList.add("hidden");
				}
			});
		})

		let overlay;

		const popper = createPopper(moreItems, moreItemsMenu, {
			strategy: "fixed",
		})

		const openMoreItemsMenu = () => {
			overlay = $("<div class='tabs__overlay'></div>")
				.click((e) => {
					$(e.currentTarget).remove();
					$(moreItemsMenu).removeClass("open");
				})
				.prependTo("body");
			$(moreItemsMenu).addClass("open");
			popper.update();
		};

		const closeMoreItemsMenu = () => {
			$(overlay).remove();
			$(moreItemsMenu).removeClass("open");
		};

		const toggleMoreItemsMenu = () => {
			if ($(moreItemsMenu).hasClass("open")) {
				closeMoreItemsMenu();
			}
			else {
				openMoreItemsMenu();
			}
		}

		// set initial state

		updateMenu();

		const activeIndex = menuItems.findIndex(menuItem => menuItem.classList.contains("active")) || 0;
		setActive(activeIndex);

		// add handlers

		new ResizeObserver(() => updateMenu()).observe(menu);

		moreItems.addEventListener("click", () => {
			toggleMoreItemsMenu();
		});

		moreItemsMenuItems.forEach((currentMenuItem, index) => {
			currentMenuItem.addEventListener("click", () => {
				closeMoreItemsMenu();
				setActive(index);
			})
		})

		menuItems.forEach((currentMenuItem, index) => {
			currentMenuItem.addEventListener("click", () => {
				setActive(index);
			})
		})
	})

	// textarea autosize

	const updateHeight = (element) => {
		const maxHeight = parseFloat(element.dataset.textareaAutosizeMaxWidth);
		const prevHeight = element.style.height;

		element.style.height = "auto";

		if (maxHeight && element.scrollHeight < maxHeight) {
			$(element).css({
				height: element.scrollHeight,
				overflowY: "hidden",
			});
		}
		else {
			$(element).css({
				height: prevHeight,
				overflowY: "auto",
			});
		}
	}

	$(".textarea-autosize").each((i, textareaAutosize) => {

		// add handlers
		$(textareaAutosize).on("input", (e) => {
			updateHeight(e.currentTarget);
		});
	})


	// with tooltip

	$(".with-tooltip").each((i, trigger) => {
		const placement = trigger.dataset.withTooltipPlacement || "top";
		const tooltip = trigger.querySelector(".tooltip");

		const popper = createPopper(trigger, tooltip, {
			placement: placement,
			strategy: "absolute",
			modifiers: [
				{
					name: "flip",
					options: {
						fallbackPlacements: ['auto'],
					},
				},
				createPlacementHandler((placement, element) => {
					$(element).removeClass("top");
					$(element).removeClass("left");
					$(element).removeClass("right");
					$(element).removeClass("bottom");
					$(element).addClass(placement);
				}),
			],
		});

		let disappearTimeoutId = -1;

		trigger.addEventListener("mouseenter", () => {
			if (tooltip.parentElement !== document.body) {
				document.body.appendChild(tooltip);
			}

			$(tooltip).addClass("show").removeClass("fade-out-slow");
			clearTimeout(disappearTimeoutId);
			popper.update();
		});

		trigger.addEventListener("mouseleave", () => {
			$(tooltip).addClass("fade-out-slow");

			disappearTimeoutId = setTimeout(() => {
				$(tooltip).removeClass("show");
			}, ANIMATION_SLOW_MS)
		});
	})

	// programs collapse

	$(".programs").each((i, programs) => {
		const expandBtn = $(programs).find(".programs__expand-btn button").get(0);
		const collapseBtn = $(programs).find(".programs__collapse-btn button").get(0);

		$(expandBtn).click(() => {
			$(programs).addClass("expand");
		});

		$(collapseBtn).click(() => {
			$(programs).removeClass("expand");
		});
	});

	// toggle button

	$('.toggle-button').on('click', function () {
		$(this).addClass('active').siblings().removeClass('active');
		const target = $($(this).closest('.toggle-button-group').attr("data-toggle-button-group-target"));
		const index = $(this).index();

		if (target.length > 0) {
			$(target).each((i, currentTarget) => {
				$(currentTarget).children().eq(index).addClass("active").siblings().removeClass("active");
			})
		}
	});

	// side menu

	let hidingTimeoutId = -1;

	const openMenu = () => {
		clearTimeout(hidingTimeoutId);
		$("#sideMenu").addClass("open").find(".side-menu__body").scrollTop(0);
		$("#header .header__burger-btn").addClass("open");
		$(document.documentElement).addClass("modal-open");
	}
	const closeMenu = () => {
		$("#sideMenu").removeClass("open").addClass("hiding");
		$("#header .header__burger-btn").removeClass("open");
		clearTimeout(hidingTimeoutId);
		hidingTimeoutId = setTimeout(() => {
			$("#sideMenu").removeClass("hiding");
		}, ANIMATION_NORMAL_MS);
		$(document.documentElement).removeClass("modal-open");
	}
	const toggleMenu = () => {
		if ($("#sideMenu").hasClass("open")) {
			closeMenu();
		}
		else {
			openMenu();
		}
	}

	$('#sideMenu .side-menu__overlay').click(closeMenu);
	$("#sideMenu .tab").click(closeMenu);
	$("#header .header__burger-btn").click(toggleMenu);

	// header

	const headerTitle = $("#header .header__title").get(0);

	if (headerTitle) {
		new ResizeObserver(() => {
			if (headerTitle.scrollWidth > headerTitle.clientWidth) {
				$(headerTitle).addClass("overflow")
			}
			else {
				$(headerTitle).removeClass("overflow")
			}
		}).observe(headerTitle)
	}

	// collapse table

	$(".collapse-table").each((i, collapseTable) => {
		const collapseBtn = $(collapseTable).find(".collapse-table__collapse-btn button").get(0);
		const expandBtn = $(collapseTable).find(".collapse-table__expand-btn button").get(0);

		$(expandBtn).click(() => {
			$(collapseTable).addClass("expand");
		})

		$(collapseBtn).click(() => {
			$(collapseTable).removeClass("expand");
		})
	})

	// line list

	$(".line-list").each((i, lineList) => {
		const menu = $(lineList).find(".line-list__menu");
		const trigger = $(lineList).find(".line-list__trigger");

		$(trigger).click(() => {
			$(lineList).toggleClass("open");
		});

		$(menu).click(() => {
			$(lineList).removeClass("open");
		});

		$(document).click((e) => {
			if ($(lineList).has(e.target).length === 0) {
				$(lineList).removeClass("open");
			}
		})
	})

	// chat

	$(".chat-list").click((e) => {
		if ($(e.target).closest(".chat-list__item-pin").length === 0) {
			const chat = $(".chat");

			$(chat).toggleClass("open");

			if ($(chat).hasClass("open")) {
				$(document.documentElement).addClass("modal-open");
			}
			else {
				$(document.documentElement).removeClass("modal-open");
			}
		}
	})

	$(".chat__close-btn").click(() => {
		$(".chat").toggleClass("open");
	})

	// youtube

	const stopVideo = (iframe) => {
		iframe.src = iframe.src;
	}

	const setSrc = (iframe) => {
		if (iframe.src !== iframe.dataset.src) {
			iframe.src = iframe.dataset.src;
		}
	}

	document.querySelectorAll(".youtube-trigger").forEach(trigger => {
		const target = document.querySelector(trigger.dataset.youtubeTriggerTarget);

		trigger.addEventListener("click", () => {
			if (target) {
				const iframe = target.querySelector("iframe");
				if (iframe) setSrc(iframe);

				document.querySelectorAll(".youtube-embedded").forEach(other => {
					other.classList.remove("open");
					other.hidden = true;
				});
				target.classList.add("open");
				target.hidden = false;
			}
		})
	})

	document.querySelectorAll(".youtube-embedded").forEach(container => {
		const overlay = container.querySelector(".youtube-embedded__overlay");
		const iframe = container.querySelector("iframe");

		container.hidden = true;

		if (overlay) {
			let hiddenTimeout = -1;
			overlay.addEventListener("click", () => {
				clearTimeout(hiddenTimeout);
				container.classList.remove("open");
				stopVideo(iframe);

				hiddenTimeout = setTimeout(() => {
					container.hidden = true;
				}, ANIMATION_SLOW_MS);
			})
		}
	})

	// cashout pool

	const expressToggler = $("#expressToggler");

	$(expressToggler).find(".toggle-button").click((e) => {
		const tooltipOn = $(expressToggler).find(".express-toggler__on-tooltip");
		const tooltipOff = $(expressToggler).find(".express-toggler__off-tooltip");

		if (e.currentTarget.getAttribute("data-value") === "express") {
			$(tooltipOn).addClass("active");
			$(tooltipOff).removeClass("active");
		}
		else {
			$(tooltipOff).addClass("active");
			$(tooltipOn).removeClass("active");
		}
	})

	$("#cashoutPoolBtn").click(() => {
		if ($(expressToggler).find(".toggle-button.active")[0].getAttribute("data-value") === "express") {
			$("#cashoutPoolExpressAlert").addClass("show");
			$("#cashoutPoolAlert").removeClass("show");
		}
		else {
			$("#cashoutPoolAlert").addClass("show");
			$("#cashoutPoolExpressAlert").removeClass("show");
		}
	})

	// form

	$("form").submit((e) => {
		e.preventDefault();
	})

	// input code
	document.querySelectorAll(".input-code > input").forEach(input => {
		input.addEventListener("input", e => {
			const input = e.currentTarget;
			const selectionStart = input.selectionStart;
			const selectionEnd = input.selectionEnd;
			input.value = input.value.replace(/\D/g, "").substring(0, 6);
			input.setSelectionRange(selectionStart, selectionEnd);
		});
	})

	// data toggle

	document.querySelectorAll("[data-toggle-class]").forEach(elem => {
		const className = elem.dataset.toggleClass;
		const targets = document.querySelectorAll(elem.dataset.target);

		if (className && targets) {
			elem.addEventListener("click", (e) => {
				console.log(targets);
				targets.forEach(target => {
					target.classList.toggle(className);
				})
			})
		}
	});

	// drag scroll
	// !IMPORTANT: data-drag-scroll must have style "overflow: <hidden | scroll>"

	window.addEventListener('load', () => {

		if (isTouchEnabled()) return;

		document.querySelectorAll("[data-drag-scroll]").forEach(elem => {
			const content = elem.querySelector("[data-drag-scroll-content]");

			new ScrollBooster({
				viewport: elem,
				content: content,
				scrollMode: 'native',
			});
		})

	});

	// all notifications

	document.querySelectorAll(".all-notifications__body").forEach(allNotifications => {
		allNotifications.addEventListener("click", (e) => {
			const notification = e.target.closest ? e.target.closest(".all-notifications__notification") : undefined;

			if (notification) {
				notification.classList.toggle("expanded");
			}
		})
	})

	// linked input

	document.querySelectorAll(".with-linked-select").forEach(elem => {
		const linkedSelect = document.querySelector(elem.getAttribute("data-linked-select"));

		if (linkedSelect) {
			linkedSelect.addEventListener("click", (e) => {
				let value = e.target.getAttribute ? e.target.getAttribute("data-value") : undefined;
				if (value !== undefined) {
					const elemItem = elem.querySelector(`[data-value=${value}]`);
					elemItem.click();
				}
			})
			elem.addEventListener("click", (e) => {
				let value = e.target.getAttribute ? e.target.getAttribute("data-value") : undefined;
				if (value !== undefined) {
					const selectItem = linkedSelect.querySelector(`[data-value=${value}]`);
					selectItem.click();
				}
			})
		}
	})

	// video player

	const videoPlayer = document.getElementById("videoPlayer");
	let prevOpen = videoPlayer.classList.contains("open");
	let prevSrc = videoPlayer.getAttribute("data-src");
	let vjs;

	const observer = new MutationObserver((mutationList) => {
		const videoPlayer = document.getElementById("videoPlayer");
		if (!videoPlayer) return;

		const open = videoPlayer.classList.contains("open");
		const src = videoPlayer.getAttribute("data-src");

		const isOpening = open && !prevOpen;
		const isClosing = !open && prevOpen;
		const isSrcChanging = src !== prevSrc;

		if ((isOpening || isSrcChanging) && src) {
			if (vjs) {
				vjs.dispose();
				vjs = undefined;
			}
			const vjsElem = videoPlayer.querySelector(".video-js");
			vjs = videojs(vjsElem, {
				controls: true,
				restoreEl: true,
				fluid: true,
				preload: "auto"
			});
			vjs.src(src);
		}
		if (isClosing) {
			if (vjs) {
				vjs.dispose();
				vjs = undefined;
			}
		}

		prevOpen = open;
		prevSrc = src;
	});

	observer.observe(videoPlayer, {
		attributes: true,
	});

	const openVideoPlayer = (src) => {
		const videoPlayer = document.getElementById("videoPlayer");
		videoPlayer.classList.add("open");
		videoPlayer.setAttribute("data-src", src);
	}
	const closeVideoPlayer = () => {
		const videoPlayer = document.getElementById("videoPlayer");
		videoPlayer.classList.remove("open");
		videoPlayer.removeAttribute("data-src");
	}

	document.querySelector("#videoPlayer .video-player__close-btn").addEventListener("click", closeVideoPlayer);
	document.querySelector("#videoPlayer .video-player__backdrop").addEventListener("click", closeVideoPlayer);

	document.querySelectorAll("[data-video-triggers-container]").forEach(container => {
		container.addEventListener("click", (e) => {
			const trigger = e.target?.closest ?
				e.target.closest("[data-video-trigger]") :
				undefined;
			const url = trigger?.getAttribute ?
				trigger?.getAttribute("data-video-trigger") :
				undefined;

			if (url) {
				openVideoPlayer(url);
			}
		})
	})

	// drag to scroll

	if (!isTouchEnabled()) {
		addDragToScrollLogic();
	} 

	// media viewer

	addMediaViewerLogic();
});
